body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

a {
    text-decoration: none;
    color: inherit;
}

hr {
    border: 1px solid #ee447d;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
}

.App__title {
    color: #ee447d;
}

.App__loading {
    color: #ee447d;
    font-weight: bold;
}

.image__wrapper {
    position: relative;
    min-width: 270px;
    min-height: 190px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
}

.image__content {
    max-height: 340px;
    max-width: 270px;
}

.image__loader {
    position: absolute;
    bottom: 10%;
    left: 40%;
    width: 50px;
}
.image__error {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 280px;
}
.card {
    align-items: flex-end;
    display: flex;
    position: relative;
    justify-content: center;
    float: left;
    margin: 10px;
    min-width: 270px;
    min-height: 190px;
    cursor: pointer;
}

.cardGrid {
    display: flex;
    flex-wrap: wrap;
    margin-left: 40px;
    margin-right: 40px;
    justify-content: space-around;
}

@media screen and (max-width: 640px) {
    .cardGrid {
        margin: 0;
    }
}

@media screen and (max-width: 500px) {
    .card {
        align-items: center;
        min-width: 320px;
    }
}
.button {
    width: 200px;
    height: 50px;
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    cursor: pointer;
    margin-left: 5px;
    margin-right: 5px;
}

.button:hover {
    opacity: 0.6;
}

.button__danger {
    background-color:#ee447d;
    color: #fff;
}

.button__primary {
    background-color: #57e99a;
    color: #fff;
}
.cardDetails {
    display: flex;
}

.cardDetails__images {
    flex: 1 1;
}

.cardDetails__info {
    flex: 3 1;
}

.cardDetails__actions {
    display: flex;
    justify-content: center;
    margin-top: 50px;
}

@media screen and (max-width: 700px) {
    .cardDetails {
        flex-direction: column;
    }
    .cardDetails__info {
        flex: 1 1;
    }
}
